.lottery-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  .cube-row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    .cube-item {
      overflow: hidden;
      .cube {
        position: relative;
        span {
          position: absolute;
          left: 50%;
          top: 50%;
          transform: translate(-50%, -50%);
        }
        span:nth-child(1) {
          top: 54%;
        }
      }
    }
  }
}
