@import './normalize.scss';

@font-face {
  font-family: 'mutouren';
  src: url('../fonts/HYZHUZIMUTOURENW-2_0.TTF');
  font-weight: normal;
  font-style: normal;
}

html,
body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

body {
  background: url('../images/bg.jpg') no-repeat center / 100% 100%;

  main {
    position: relative;
    width: 1280px;
    height: 100%;
    margin: 0 auto;
  }
}

.role {
  position: absolute;
  bottom: 80px;
  z-index: 10;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;

  &.left {
    left: 0;
    width: 350px;
    height: 500px;
    background-image: url('../images/role-1.png');
  }

  &.right {
    right: 0;
    width: 350px;
    height: 370px;
    background-image: url('../images/role-2.png');
  }
}

.info-bar {
  position: absolute;
  top: 120px;
  left: 0;
  font-size: 16px;
  color: #E3E6ED;
  div {
    width: 190px;
    height: 36px;
    margin-bottom: 4px;
    line-height: 36px;
    text-align: center;
    background: url('../images/bar-row.png') no-repeat center / 100% 100%;
    span {
      color: #ffd34f;
      font-family: mutouren;
    }
  }
}

.button-audio {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 0;
  top: 120px;
  z-index: 10;
  width: 32px;
  height: 32px;
  background: url('../images/billet.png') no-repeat center / 100% 100%;
  span {
    width: 14px;
    height: 14px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }
  &.play span {
    background-image: url('../images/play.png');
  }
  &.pause span {
    background-image: url('../images/pause.png');
  }
}

.draw-container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 780px;
  height: 520px;
  background: url('../images/bar-lg.png') no-repeat center / 100% 100%;

  .lottery-title {
    display: flex;
    justify-content: space-between;
    position: absolute;
    top: 120px;
    left: 50%;
    transform: translateX(-50%);
    width: 70%;
    span {
      width: 81.89px;
      text-align: center;
      font-size: 30px;
      font-family: mutouren;
      color: #511611;
    }
  }

  #lottery {
    position: absolute;
    top: 180px;
    left: 50%;
    transform: translateX(-50%);
    width: 70%;
  }

  .treasure {
    position: absolute;
    top: -100px;
    width: 280px;
    height: 200px;
  }
  .crystal {
    position: absolute;
    right: 0;
    z-index: 2;
    top: -40px;
    width: 150px;
    height: 130px;
  }
  .role {
    position: absolute;
    top: -140px;
    right: 60px;
    z-index: 1;
    width: 200px;
    height: 230px;
  }

  .title-box {
    position: absolute;
    left: 50%;
    top: -160px;
    z-index: 10;
    transform: translateX(-50%);
    width: 500px;
    height: 300px;
    background: url('../images/title-bg.png') no-repeat center / 100% 100%;

    .count {
      position: absolute;
      top: 86px;
      width: 500px;

      >span {
        display: block;
        position: absolute;
        width: 100%;
        text-align: center;
        font-size: 24px;
        font-weight: bold;

        &:nth-child(1) {
          top: 8px;
          color: #11255a;

          span {
            color: #11255a;
          }
        }

        &:nth-child(2) {
          top: 4px;
          color: #cd3c4b;

          span {
            color: #a93c53;
          }
        }

        &:nth-child(3) {
          color: #ffd34f;

          span {
            color: #fd314a;
          }
        }

        span {
          font-family: mutouren;
          font-size: 50px;
        }
      }

    }

    .title {
      position: absolute;
      bottom: 72px;
      width: 500px;
      height: 82px;
      font-family: mutouren;
      font-size: 72px;

      span {
        display: block;
        position: absolute;
        width: 100%;
        text-align: center;
      }

      span:nth-child(1) {
        top: 8px;
        color: #11255a;
      }

      span:nth-child(2) {
        top: 4px;
        color: #cd3c4b;
      }

      span:nth-child(3) {
        color: #ffd34f;
      }
    }
  }
}
